.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #007BFF;
    animation: spin 1s ease infinite;
    margin: auto;
    margin-bottom: 6px;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

video {
  border-radius: 50%;
  object-fit: cover;
}